import { OperacionesHttpProvider } from './../providers/operaciones-http/operaciones-http';
import { Injectable } from '@angular/core';
import { FormControl } from '@angular/forms';
import { IfObservable } from 'rxjs/observable/IfObservable';
import { Observable } from 'rxjs/Observable';


@Injectable()
export class validacionesAsincronas {

    debouncer: any;

    constructor(public operacionesHttp: OperacionesHttpProvider) {

    }

    validarNombreUsuario(control: FormControl): any {
        clearTimeout(this.debouncer);
        return new Promise(resolve => {
            //this.debouncer = setTimeout(() => {
            this.operacionesHttp.validarCamposRegistro("nombreUsuario", control.value).subscribe((res) => {
                if (res.ok) {
                    resolve(null);
                }
            }, (err) => {
                if (err._body == "existe") {
                    console.log();
                    resolve({ 'nombreUsuarioInUse': true });
                } else {
                    resolve(null);
                }
            });
            //}, 3000);
        });
    }

    validarNumeroIdentificacion(control: FormControl): any {
        clearTimeout(this.debouncer);
        return new Promise(resolve => {
            //this.debouncer = setTimeout(() => {
            this.operacionesHttp.validarCamposRegistro("numeroIdentificacion", control.value).subscribe((res) => {
                if (res.ok) {
                    resolve(null);
                }
            }, (err) => {
                if (err._body == "existe") {
                    console.log();
                    resolve({ 'identificacionInUse': true });
                } else {
                    resolve(null);
                }
            });
            //}, 3000);
        });
    }

    validarTelefono(control: FormControl): any {
        clearTimeout(this.debouncer);
        return new Promise(resolve => {
            //this.debouncer = setTimeout(() => {
            this.operacionesHttp.validarCamposRegistro("telefono", control.value).subscribe((res) => {
                if (res.ok) {
                    resolve(null);
                }
            }, (err) => {
                if (err._body == "existe") {
                    console.log();
                    resolve({ 'telefonoInUse': true });
                } else {
                    resolve(null);
                }
            });
            //}, 3000);
        });
    }
}


